"use client";
import { useState } from 'react';
import SimulationCanvas from '../components/SimulationCanvas';
import StartScreen from '../components/StartScreen';

export default function Home() {
    const [hasStarted, setHasStarted] = useState(false);
    const [initialData, setInitialData] = useState<string | null>(null);

    const handleStartNew = () => {
        setInitialData(null);
        setHasStarted(true);
    };

    const handleLoad = (data: string) => {
        setInitialData(data);
        setHasStarted(true);
    };

    return (
        <main className="min-h-screen bg-pastel-bg p-4 lg:p-8 font-sans text-slate-800">
            <div className="max-w-7xl mx-auto">
                <header className="mb-8 flex items-center justify-between">
                    <div>
                        <h1 className="text-4xl font-black text-transparent bg-clip-text bg-gradient-to-r from-indigo-500 to-purple-600 mb-2">
                            VolverE
                        </h1>
                        <p className="text-slate-500 font-medium">Evolutionary Simulation Engine</p>
                    </div>
                    {hasStarted && (
                        <button
                            onClick={() => setHasStarted(false)}
                            className="text-sm text-slate-400 hover:text-indigo-500 transition-colors"
                        >
                            Back to Menu
                        </button>
                    )}
                </header>

                {!hasStarted ? (
                    <StartScreen onStartNew={handleStartNew} onLoad={handleLoad} />
                ) : (
                    <SimulationCanvas initialWorldData={initialData} />
                )}
            </div>
        </main>
    );
}
